<!-- eslint-disable vue/valid-template-root -->
<template>
  <NavBar />
  <div class="container">
    <div class="row">
      <div class="col-8">
        <h1 class="card-title" v-bind:title="fooditem.title">
          {{ fooditem.title }}
        </h1>
        <img :src="fooditem.image" alt=" image cap" />
      </div>
      <div class="col-4">
        <div style="padding-top: 6rem">
          <h2 v-bind:desc="fooditem.title">
            {{ fooditem.title }}
          </h2>
          <p v-bind:desc="fooditem.desc">
            {{ fooditem.desc }}
          </p>
          <rate-food :food="fooditem" />
        </div>

        <!-----v-for="(skill, index) in skills.length"
      :key="skill.name"
      :index="index"
      :names-array="skills.map(a => a.name)"------>
      </div>
    </div>
  </div>
</template>
<script>
import RateFood from "./RateFood.vue";
import NavBar from "@/components/NavBar.vue";
export default {
  components: { RateFood, NavBar },
  props: [],
  data() {
    return {
      fooditem: [{}],
      foodId: this.$route.params.fid,
    };
  },

  created: async function () {
    let fId = this.foodId;
    console.log(this.foodId);
    fetch(`http://127.0.0.1:8000/food/foodentry/${fId}`, {
      method: "GET",
      headers: {
        "Content-Type": "Application/json",
        Authorization: `${this.Token}`,
      },
    })
      .then((resp) => resp.json())
      .then((resp) => (this.fooditem = resp))

      .catch((error) => console.log(error));
  },
  methods: {},
};
</script>
<style scoped>
img {
  width: 100%;
  height: auto;
  margin: auto;
}

.row {
  border: 2px solid black;
}
span {
  cursor: pointer;
}
.bigger {
  font-size: 2rem;
  cursor: pointer;
}
.orange {
  color: orange;
}
</style>
