<template>
  <div class="container">
    <button @click="logout" class="btn btn-outline-danger float-end">
      Logout
    </button>

    <!--- <button class="btn btn-outline-primary float-end">Register</button>--->
  </div>
</template>
<script>
// Import Swiper styles
import "swiper/css";

export default {
  components: {},
  props: [],
  methods: {
    logout() {
      this.$cookies.remove("food-token");
      this.$router.push("/userauth");
    },
  },
};

// @ is an alias to /src
</script>
