<template>
  <NavBar />
  {{ u }}
  <div class="container">
    <div class="row">
      <div class="col-8">
        <h3 class="center">Delicious Foods</h3>
      </div>
      <div class="col-4">
        <AuthButtons />
      </div>
    </div>
    <div class="row my-2">
      <ImageSlider />
    </div>
  </div>

  <FoodItem />
</template>

<script>
import NavBar from "@/components/NavBar.vue";
import ImageSlider from "@/components/ImageSlider.vue";
import FoodItem from "./FoodItem.vue";

// Import Swiper styles
import "swiper/css";
import AuthButtons from "@/components/AuthButtons.vue";

export default {
  components: {
    NavBar,
    ImageSlider,
    FoodItem,
    AuthButtons,
  },
  data() {},
  methods: {
    logout() {
      this.$cookies.remove("food-token");
      this.$router.push("/userauth");
    },
    getUser() {
      fetch("http://127.0.0.1:8000/user/", {
        method: "GET",
        headers: {
          "Content-Type": "Application/json",
          Authorization: `${this.Token}`,
        },
      })
        .then((resp) => resp.json())
        .then((resp) => (this.u = resp))
        .then((user) => console.log(user))
        .catch((error) => console.log(error));
    },
  },
  created: async function () {
    this.getUser();
  },
};

// @ is an alias to /src
</script>
