<!-- eslint-disable vue/valid-template-root -->
<template>
  <NavBar />

  <div class="container mt-5">
    <div class="row">
      <h2>Authentication</h2>
      <form action="">
        <!-- Username input -->
        <div class="form-outline mb-4">
          <input type="username" class="form-control" v-model="username" />
          <label class="form-label">Username</label>
        </div>

        <!-- Password input -->
        <div class="form-outline mb-4">
          <input type="password" class="form-control" v-model="password" />
          <label class="form-label">Password</label>
        </div>
        <!-- Submit button -->

        <button
          type="button"
          @click="login"
          class="btn btn-primary btn-block mb-4 mx-2"
          v-if="loginmode"
        >
          Login
        </button>
        <button
          @click="register"
          type="button"
          class="btn btn-primary btn-block mb-4"
          v-else
        >
          Register
        </button>
        <br />
        <p class="btn btn-success" @click="loginmode = false" v-if="loginmode">
          Dont have account yet? Create One
        </p>

        <p
          class="btn btn-success"
          @click="loginmode = true"
          $emit(this.username)
          v-else
        >
          Already have one?Login Here
        </p>
      </form>
    </div>
  </div>
</template>
<script>
import NavBar from "@/components/NavBar.vue";
export default {
  components: {
    NavBar,
  },
  props: { userName: String },
  data() {
    return {
      username: "",
      password: "",
      loginmode: true,
      user: "",
    };
  },
  created: async function () {
    if (this.$cookies.isKey("food-token")) {
      this.$router.push("/");
    }
  },
  methods: {
    login: async function () {
      fetch("http://127.0.0.1:8000/auth/", {
        method: "POST",
        headers: {
          "Content-Type": "Application/json",
        },
        body: JSON.stringify({
          username: this.username,
          user: this.username,
          password: this.password,
        }),
      })
        .then((resp) => resp.json())

        .then((res) => {
          if (res.token != undefined)
            this.$cookies.set("food-token", res.token, "30d");
          this.$router.push("/");
        })
        .catch((error) => console.log(error));
    },
    register: async function () {
      fetch("http://127.0.0.1:8000/food/users/", {
        method: "POST",
        headers: {
          "Content-Type": "Application/json",
        },
        body: JSON.stringify({
          username: this.username,
          password: this.password,
        }),
      })
        .then(() => {
          this.login();
        })
        .catch((error) => console.log(error));
    },
  },
};
</script>
<style></style>
