<!-- eslint-disable vue/valid-template-root -->
<template>
  <div class="container my-2">
    <div class="row">
      <div
        v-for="food in foodentry"
        :key="food"
        class="card col-sm-12 col-md-6 col-lg-4 mx-auto my-2"
        style="width: 22rem"
      >
        <img class="card-img-top" :src="food.image" alt="Card image cap" />
        <div class="card-body">
          <h5 class="card-title" v-bind:title="food.title">
            {{ food.title }}
          </h5>
          <p class="card-text" v-bind:desc="food.desc">
            {{ food.desc }}
          </p>
          <button btn btn-success>
            <RouterLink :to="`/itemdetail/${food.id}`">
              View Details!</RouterLink
            >
            <!--     <RouterLink :to="`/ratefood/${food.id}`"> Rate This!</RouterLink>-->
          </button>
          <rate-food :token="this.token" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      foodentry: [],
      d: 5,
      token: "",
    };
  },
  methods: {
    getFoodItem() {
      fetch("http://127.0.0.1:8000/food/foodentry/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.token}`,
        },
      })
        .then((resp) => resp.json())
        .then((resp) => (this.foodentry = resp))
        .catch((error) => console.log(error));
    },
  },

  created: async function () {
    if (this.$cookies.isKey("food-token")) {
      this.token = this.$cookies.get("food-token");
      console.log("token is: ", this.token);
      this.getFoodItem();
    } else {
      this.$router.push("/userauth");
    }
  },
};
</script>
<style>
img {
  width: 100%;
  height: 45%;
}
</style>
