<!-- eslint-disable vue/valid-template-root -->
<template>
  <!--<p>{{ food }}</p>--->
  <div>
    <i
      class="fa-solid fa-star"
      :class="[food.avg_rating > 0 ? 'orange' : '']"
    ></i>
    <i
      class="fa-solid fa-star"
      :class="[food.avg_rating > 1 ? 'orange' : '']"
    ></i>
    <i
      class="fa-solid fa-star"
      :class="[food.avg_rating > 2 ? 'orange' : '']"
    ></i>
    <i
      class="fa-solid fa-star"
      :class="[food.avg_rating > 3 ? 'orange' : '']"
    ></i>
    <i class="fa-solid fa-star" :class="[avg_rating > 4 ? 'orange' : '']"></i>
    <span>({{ food.no_of_ratings }})</span>
  </div>
  <br /><br />
  <div>
    <h4>Rate This!!</h4>
    <span>
      <i
        class="fa-solid fa-star bigger"
        v-for="star in stars"
        :key="star"
        :class="[highlighted > star - 1 ? 'purple' : '']"
        @mouseenter="highlighted = star"
        @mouseleave="highlighted = -1"
        @click="rateClicked(star)"
      ></i>
    </span>
  </div>
</template>
<script>
export default {
  components: {},
  props: ["food"],
  data() {
    return {
      stars: [0, 1, 2, 3, 4],
      highlighted: 4,
      token: "",
    };
  },
  methods: {
    rateClicked(rate) {
      console.log("foodId", this.food.id);
      fetch(`http://127.0.0.1:8000/food/foodentry/${this.food.id}/rate_food/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.token}`,
        },

        body: JSON.stringify({ stars: rate + 1 }),
      })
        .then((resp) => resp.json())
        .then((resp) => {
          this.$emit("update");
          console.log("Issue is", resp);
        })
        .catch((error) => console.log(error));
    },
  },
  created: async function () {
    this.token = this.$cookies.get("food-token");
    console.log("token is: ", this.token);
  },
};
</script>
<style scoped>
img {
  width: 100%;
  height: auto;
  margin: auto;
}

.row {
  border: 2px solid black;
}
span {
  cursor: pointer;
}
.bigger {
  font-size: 2rem;
  cursor: pointer;
}
.orange {
  color: orange;
}
.purple {
  color: yellow;
}
</style>
